import {
	DictionaryList,
	LanguageOptions,
} from '../services/constants/languages'
import PropTypes, { InferProps } from 'prop-types'
import { createContext, useState } from 'react'

import { LanguageList } from '../services/constants'
import { Portfolio } from '../utils/Portfolio'

const LanguageContext = createContext({
	userLanguage: LanguageList.en,
	dictionary: DictionaryList.en,
	portfolio: Portfolio.en,
	userLanguageChange: (e: LanguageList) => {},
})

const LanguageProviderPropTypes = {
	children: PropTypes.node.isRequired,
}

type LanguageProviderTypes = InferProps<typeof LanguageProviderPropTypes>

const LanguageProvider: React.FC<LanguageProviderTypes> = ({ children }) => {
	const defaultLanguage: LanguageList = window.localStorage.getItem(
		'rcml-lang'
	) as LanguageList
	const [userLanguage, setUserLanguage] = useState<LanguageList>(
		defaultLanguage || LanguageList.en
	)

	const userLanguageChange = (selected: LanguageList) => {
		const newLanguage = LanguageOptions[selected]
			? selected
			: LanguageList.en
		setUserLanguage(newLanguage)
		window.localStorage.setItem('rcml-lang', newLanguage)
	}

	const provider = {
		userLanguage,
		dictionary: DictionaryList[userLanguage],
		portfolio: Portfolio[userLanguage],
		userLanguageChange,
	}

	return (
		<LanguageContext.Provider value={provider}>
			{children}
		</LanguageContext.Provider>
	)
}

export { LanguageProvider, LanguageContext }
