import './About.css'

import { useContext, useRef } from 'react'

import Avatar from '@material-ui/core/Avatar'
import CvOneSide from '../CvOneSide/CvOneSide'
import GitHubIcon from '@material-ui/icons/GitHub'
import { LanguageContext } from '../../contexts/language'
import LinkedInIcon from '@material-ui/icons/LinkedIn'
import ReactToPrint from 'react-to-print'
import { ThemeContext } from '../../contexts/theme'
import { ThemeName } from '../../services/constants'
import profileDark from '../../assets/images/profile-dark.jpg'
import profileLight from '../../assets/images/profile-light.jpg'

const About = () => {
	const { themeName } = useContext(ThemeContext)
	const { dictionary, portfolio } = useContext(LanguageContext)
	const { name, role, description, social } = portfolio.about
	const componentRef = useRef<HTMLDivElement>(null)

	return (
		<div className='about center'>
			<Avatar
				alt='Piero Santisteban'
				src={themeName === ThemeName.DARK ? profileDark : profileLight}
				className='about__avatar'
			/>

			{name && (
				<h1>
					{dictionary.hi}, {dictionary.im}{' '}
					<span className='about__name'>{name}.</span>
				</h1>
			)}

			{role && <h2 className='about__role'>{role}.</h2>}
			<p className='about__desc'>{description && description}</p>

			<div className='about__contact center'>
				<div>
					<ReactToPrint
						trigger={() => (
							<a href='#' download>
								<span className='btn btn--outline'>
									{dictionary.resume}
								</span>
							</a>
						)}
						content={() => componentRef.current}
						documentTitle='Piero Santisteban - Resume'
					/>
					<div style={{ display: 'none' }}>
						<CvOneSide ref={componentRef} />
					</div>
				</div>
				{social && (
					<>
						{social.github && (
							<a
								href={social.github}
								target='_blank'
								rel='noreferrer'
								aria-label='github'
								className='link link--icon'
							>
								<GitHubIcon />
							</a>
						)}

						{social.linkedin && (
							<a
								href={social.linkedin}
								target='_blank'
								rel='noreferrer'
								aria-label='linkedin'
								className='link link--icon'
							>
								<LinkedInIcon />
							</a>
						)}
					</>
				)}
			</div>
		</div>
	)
}

export default About
