import './CvOneSide.css'

import {ICertificate, IEducation, IExperience} from '../../utils/Portfolio'
import React, { forwardRef, useContext } from 'react'

import { LanguageContext } from '../../contexts/language'
import uniqid from 'uniqid'

interface Props {
	//children?: ReactNode;
	//type: "submit" | "button";
}
export type Ref = HTMLDivElement

const CvOneSide = forwardRef<Ref, Props>((props, ref) => {
	const { dictionary, portfolio } = useContext(LanguageContext)
	const {
		about,
		contact,
		experience,
		education,
		skills,
		languages,
		softSkills,
		certificates,
	} = portfolio

	return (
		<div className='cv_one_side light' ref={ref}>
			<h2 className='complete-name'>{about.completeName}</h2>
			<p>
				<a href={`mailto:${contact.email}`}>{contact.email}</a> |{' '}
				{contact.phone} | {contact.location} |{' '}
				<a href={contact.website}>{contact.website}</a>
			</p>
			<section className='section'>
				<h3 className='section_title'>{dictionary.experience}</h3>
				{experience.map((item) => (
					<ExperienceItem key={uniqid()} {...item} />
				))}
			</section>

			<section className='section'>
				<h3 className='section_title'>{dictionary.education}</h3>
				{education.map((item) => (
					<EducationItem key={uniqid()} {...item} />
				))}
			</section>
			<section className='section'>
				<h3 className='section_title'>{dictionary.skills}</h3>
				<ul className='section_item_description_list'>
					<li className='section_item_description_list_item'>
						<span style={{ fontWeight: 'bold' }}>
							{dictionary.technicalSkills}:
						</span>{' '}
						{skills.join(', ')}
					</li>
					<li className='section_item_description_list_item'>
						<span style={{ fontWeight: 'bold' }}>
							{dictionary.softSkills}:
						</span>{' '}
						{softSkills.join(', ')}
					</li>
					<li className='section_item_description_list_item'>
						<span style={{ fontWeight: 'bold' }}>
							{dictionary.languages}:
						</span>{' '}
						{languages
							.map(
								(language) =>
									`${language.name} (${language.level})`
							)
							.join(', ')}
					</li>
				</ul>
			</section>
			<section className='section'>
				<h3 className='section_title'>{dictionary.certifications}</h3>
				<div className='d-flex flex-row flex-wrap'>
				{certificates.map((item) => (
					<CertificateItem key={uniqid()} {...item} />
				))}
				</div>
			</section>
		</div>
	)
})

const CertificateItem = (item: ICertificate) => {
	return (
		<section className='certificate_item col-6 pe-4 mb-2 pb-1'>
			<div className='d-flex'>
				<p className='section_item_title flex-grow-1'>
					{item.name}
				</p>
				<p className='section_item_date text_end' style={{whiteSpace: 'nowrap'}}>{item.date}</p>
			</div>
			<div className='d-flex'>
				<p className='section_item_subtitle flex-grow-1'>
					{item.organization}
				</p>
				<p className='section_item_location'>{}</p>
			</div>
			<div className='d-flex'>
				<p className='section_item_subtitle flex-grow-1'>
					{item.description}
				</p>
			</div>
		</section>
	)
}

const ExperienceItem = (item: IExperience) => {
	return (
		<section className='section_item'>
			<div className='d-flex'>
				<p className='section_item_title flex-grow-1'>
					{item.institution}
				</p>
				<p className='section_item_date'>{item.date}</p>
			</div>
			<div className='d-flex'>
				<p className='section_item_subtitle flex-grow-1'>
					{item.title}
				</p>
				<p className='section_item_location'>{item.location}</p>
			</div>
			<ul className='section_item_description_list'>
				{item.description.map((description) => (
					<li
						key={uniqid()}
						className='section_item_description_item'
					>
						{description}
					</li>
				))}
			</ul>
		</section>
	)
}

const EducationItem = (item: IEducation) => {
	return (
		<section className='section_item'>
			<div className='d-flex'>
				<p className='section_item_title flex-grow-1'>
					{item.institution}
				</p>
				<p className='section_item_date'>{item.date}</p>
			</div>
			<div className='d-flex'>
				<p className='section_item_subtitle flex-grow-1'>
					{item.title}
				</p>
			</div>
		</section>
	)
}

export default CvOneSide
