import './Navbar.css'

import { MenuItem, Select } from '@material-ui/core'
import { useContext, useState } from 'react'

import Brightness2Icon from '@material-ui/icons/Brightness2'
import CloseIcon from '@material-ui/icons/Close'
import { LanguageContext } from '../../contexts/language'
import { LanguageList } from '../../services/constants'
import { LanguageOptions } from '../../services/constants/languages'
import MenuIcon from '@material-ui/icons/Menu'
import { ThemeContext } from '../../contexts/theme'
import WbSunnyRoundedIcon from '@material-ui/icons/WbSunnyRounded'
import useMediaQuery from '@material-ui/core/useMediaQuery'

const Navbar = () => {
	const { themeName, toggleTheme } = useContext(ThemeContext)
	const {
		userLanguage,
		dictionary,
		portfolio,
		userLanguageChange,
	} = useContext(LanguageContext)
	const { contact, education, experience, projects, skills } = portfolio
	const [showNavList, setShowNavList] = useState(false)

	const breakpointXS = useMediaQuery('(max-width:600px)')
	const toggleNavList = () => setShowNavList(!showNavList)

	return (
		<nav className='center nav'>
			<ul
				style={{ display: showNavList ? 'flex' : undefined }}
				className='nav__list'
			>
				{education.length ? (
					<li className='nav__list-item'>
						<a
							href='#education'
							onClick={toggleNavList}
							className='link link--nav'
						>
							{dictionary.education}
						</a>
					</li>
				) : null}

				{experience.length ? (
					<li className='nav__list-item'>
						<a
							href='#experience'
							onClick={toggleNavList}
							className='link link--nav'
						>
							{dictionary.experience}
						</a>
					</li>
				) : null}

				{projects.length ? (
					<li className='nav__list-item'>
						<a
							href='#projects'
							onClick={toggleNavList}
							className='link link--nav'
						>
							{dictionary.projects}
						</a>
					</li>
				) : null}

				{skills.length ? (
					<li className='nav__list-item'>
						<a
							href='#skills'
							onClick={toggleNavList}
							className='link link--nav'
						>
							{dictionary.skills}
						</a>
					</li>
				) : null}

				{contact.email ? (
					<li className='nav__list-item'>
						<a
							href='#contact'
							onClick={toggleNavList}
							className='link link--nav'
						>
							{dictionary.contact}
						</a>
					</li>
				) : null}
			</ul>

			<Select
				className='select-language'
				value={userLanguage}
				onChange={(e) =>
					userLanguageChange(e.target.value as LanguageList)
				}
			>
				{Object.entries(LanguageOptions).map(([id, language]) => (
					<MenuItem key={id} value={id}>
						{language.flag}&nbsp; {!breakpointXS && language.name}
					</MenuItem>
				))}
			</Select>

			<button
				type='button'
				onClick={toggleTheme}
				className='btn btn--icon nav__theme'
				aria-label='toggle theme'
			>
				{themeName === 'dark' ? (
					<WbSunnyRoundedIcon />
				) : (
					<Brightness2Icon />
				)}
			</button>

			<button
				type='button'
				onClick={toggleNavList}
				className='btn btn--icon nav__hamburger'
				aria-label='toggle navigation'
			>
				{showNavList ? <CloseIcon /> : <MenuIcon />}
			</button>
		</nav>
	)
}

export default Navbar
