import './Experiences.css'

import ExperienceItem from './ExperienceItem/ExperienceItem'
import { LanguageContext } from '../../contexts/language'
import uniqid from 'uniqid'
import { useContext } from 'react'

const Experiences = () => {
	const { dictionary, portfolio } = useContext(LanguageContext)
	const { experience } = portfolio
	if (!experience.length) return null

	return (
		<section id='experience' className='section experiences'>
			<h2 className='section__title'>{dictionary.experience}</h2>
			<ul className='experience__grid'>
				{experience.map((work) => (
					<ExperienceItem key={uniqid()} work={work} />
				))}
			</ul>
		</section>
	)
}

export default Experiences
